.container {
	width: 144px;
	height: 48px;

	display: flex;
	justify-content: center;
	align-items: center;

	cursor: pointer;

	border: 2px solid #ebae12;
	border-radius: 36px;

	box-shadow: inset 0 0 #ebae12;
	transition: all 0.7s ease-out;
	&:hover {
		box-shadow: inset 12em 0 #ebae12;
	}
}


.title {
	font-size: 1rem;
	font-weight: bold;
}
